import React, { useState } from 'react'
import style from '../Profile.module.scss'
import { Col, Container, Row } from 'react-bootstrap'
import RatingAndCommentCounter from '../../common/RatingAndCommentCounter/RatingAndCommentCounter'
import SimpleSlider from '../../../forTesting/Slick/SimpleSlider'
import { generateText } from '../../../validators/linkValidator'
import Text from '../../common/Text/Text'
import ImageSlider from '../../common/ImageSlider/ImageSlider'

const AboutSection = () => {
	const _text =
		'Всем привет!)) Меня зовут Слава и я разрабатываю этот ' +
		'замечательный портал. Точнее я занимаюсь разработкой ' +
		'front-части. Забавно, но разработкой back-части занимается ' +
		'другой Слава))). Итак, тут мне нужно проверить как будет ' +
		'выглядеть текст в описании, поэтому я чуть побуду графоманом ' +
		'(https://ru.wikipedia.org/wiki/%D0%93%D1%80%D0%B0%D1%84%D0%BE%D0%BC%D0%B0%D0%BD%D0%B8%D1%8F) ' +
		'Да, мне нужно проверить как будет выглядеть и такая херь)))) ' +
		'Так вот, я использую ReactJS для создания сего портала. Мне так ' +
		'же помогают нативный JS и препроцессор SCSS (ох уж и крутая ' +
		'штука). А на бэке крутится Scala + PlayFramework + MySQL, но эт ' +
		'уже другая история) Оч надеюсь, что этот проект будет успешно ' +
		'завершен и мы сделает этот мир чуть лучше))) Аминь))'

	const [textState, setTextState] = useState(generateText(_text, false, 50))

	return (
		<div className={`${style.aboutSection} ${style.Card}`}>
			<div className={`${style.header} ${style.header_margin}`}>
				Обшая информация
			</div>
			<Container>
				<Row>
					<Col sm={12} md={6} lg={4} xl={3}>
						<div className={style.avatar}>
							<img src='avatars/avaTestMyPhoto.jpg' alt='' />
						</div>
					</Col>

					<Col sm={12} md={6} lg={4} xl={4}>
						<div className={style.info}>
							<div className={style.fullName}>
								<span className={style.surname}>Фамилия</span>
								<span className={style.namePatronymic}>Имя Отчество</span>
							</div>

							<div>Специализация (3-глубина)</div>

							<div className={style.ratingAndCommentCounterBox}>
								<RatingAndCommentCounter
									rating={'5,0'}
									commentsCounter={'98'}
								/>
							</div>
						</div>
					</Col>

					<Col sm={12} md={12} lg={4} xl={5}>
						<div className={style.description}>
							<Text
								textState={textState}
								setTextState={setTextState}
								_text={_text}
							/>
						</div>
					</Col>

					<Col sm={12} md={12} lg={12}>
						<div className={style.sliderSection}>
							<ImageSlider
								photos={[
									'avatars/2Z9k75SIbYU.jpg',
									'avatars/1525374264_7f85e7b.jpeg',
									'avatars/ariana_grande.jpg',
									'avatars/avatar2700.jpg',
									'avatars/avatar-zabavnogo-malisha-v-ochkah.jpg',
									'avatars/avaTestMyPhoto.jpg',
									'avatars/boy.jpg',
								]}
							/>
						</div>
					</Col>
				</Row>
			</Container>
		</div>
	)
}

export default AboutSection
